document.addEventListener("DOMContentLoaded", () => {
	// Includes
	// // Variables
const body = document.querySelector('.body');
const isMobileDevice = (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent));
const isSmoothScrollSupported = 'scrollBehavior' in document.documentElement.style;
const media = window.matchMedia("(max-width: 767px)");;
	// // Functions
function log() {
	console.log('Hello App');
}
log();

function showActiveLink(links, activeClass, offsetDifference) {
	for (let i = links.length - 1; i >= 0; i--) {
		let target = document.querySelector(links[i].hash);
		if (window.scrollY > target.offsetTop - offsetDifference) {
			if (document.querySelector(`.${activeClass}`)) {
				document.querySelector(`.${activeClass}`).classList.remove(`${activeClass}`);
			}
			links[i].classList.add(`${activeClass}`);
			break;
		} else {
			if (document.querySelector(`.${activeClass}`)) {
				document.querySelector(`.${activeClass}`).classList.remove(`${activeClass}`);
			}
		}
	}
}

function scrollToTarget(id, media) {
	const target = document.querySelector(id);
	if (!isSmoothScrollSupported && target !== null) {
		const bodyRect = document.body.getBoundingClientRect();
		const elemRect = target.getBoundingClientRect();
		const offset = elemRect.top - bodyRect.top;

		const requestAnimFrame = (function () {
			return window.requestAnimationFrame || window.webkitRequestAnimationFrame || window.mozRequestAnimationFrame || function (callback) {
				window.setTimeout(callback, 1000 / 60);
			};
		})();
		Math.easeInOutQuad = function (t, b, c, d) {
			t /= d / 2;
			if (t < 1) {
				return c / 2 * t * t + b
			}
			t--;
			return -c / 2 * (t * (t - 2) - 1) + b;
		};
		function scrollToIos(to, callback, duration) {
			function move(amount) {
				document.documentElement.scrollTop = amount;
				document.body.parentNode.scrollTop = amount;
				document.body.scrollTop = amount;
			}

			function position() {
				return document.documentElement.scrollTop || document.body.parentNode.scrollTop || document.body.scrollTop;
			}
			let start = position(),
				change = to - start,
				currentTime = 0,
				increment = 20;
			duration = (typeof (duration) === 'undefined') ? 500 : duration;

			let animateScroll = function () {
				currentTime += increment;
				let val = Math.easeInOutQuad(currentTime, start, change, duration);
				move(val);
				if (currentTime < duration) {
					requestAnimFrame(animateScroll);
				} else {
					if (callback && typeof (callback) === 'function') {
						callback();
					}
				}
			};
			animateScroll();
		}
		scrollToIos(offset - 50, null, 500);
		return;
	}

	if (target !== null) {
		let pos = target.offsetTop - (media.matches ? 50 : 80);
		window.scrollTo({
			top: pos,
			behavior: 'smooth'
		});
	}
};
	// // MODAL
// vars
const modalButtons = document.querySelectorAll('.modal-btn');
const lockPadding = document.querySelectorAll('.fixed-padding');
const modalCloseButtons = document.querySelectorAll('.close-modal');
const timeout = 800;
let currentCloseBtn;
let unlock = true;
let isMultipleClick = false;
let bodyWasNotLock;

// Проверки
if (typeof (disableScroll) !== 'function' && typeof (enableScroll) !== 'function') {
	console.log(`Тип данных переменных "disableScroll" и "enableScroll": "${typeof (disableScroll)} и ${typeof (enableScroll)}"! Для IOS НЕ будет выполнятся скрипт scrollLockIOS`);
}
// -- //
if (modalButtons.length > 0) {
	for (let index = 0; index < modalButtons.length; index++) {
		const modalCurrentButton = modalButtons[index];
		modalCurrentButton.addEventListener("click", function (e) {
			const modalName = modalCurrentButton.dataset.path;
			const curentModal = document.getElementById(modalName);
			if (curentModal != null) {
				modalOpen(curentModal);
				setTimeout(() => {
					modalCloseButtons.forEach((el) => {
						el.focus()
					})
				}, 100)
				e.preventDefault();
			} else {
				console.log(`Модальное окно не существует! ${curentModal}`);
			}
		});
	}
}

if (modalCloseButtons.length > 0) {
	for (let index = 0; index < modalCloseButtons.length; index++) {
		const el = modalCloseButtons[index];
		currentCloseBtn = el;
		el.addEventListener('click', function (e) {
			modalClose(el.closest('.modal'));
			e.preventDefault();
		});
	}
}
let previousActiveElement;
function modalOpen(curentModal) {
	if (curentModal && unlock) {
		previousActiveElement = document.activeElement;
		const modalActive = document.querySelector('.modal.open');
		if (modalActive) {
			modalClose(modalActive, false);
		}
		else {
			bodyLock();
		}
		curentModal.classList.add('open');

		curentModal.addEventListener("mousedown", function (e) {
			if (isMultipleClick === false) {
				if (!e.target.closest('.modal__content')) {
					modalClose(e.target.closest('.modal'));
				}
				isMultipleClick = true;
			} else {
				setTimeout(() => {
					isMultipleClick = false;
				}, timeout);
			}
		});
	}
}
function modalClose(modalActive, doUnlock = true) {
	if (unlock) {
		modalActive.classList.remove('open');
		if (doUnlock) {
			bodyUnlock();
		}
	}
	setTimeout(function () {
		previousActiveElement.focus();
	}, timeout);

}

function bodyLock() {
	const lockPaddingValue = window.innerWidth - body.offsetWidth + 'px';
	if (lockPadding.length > 0) {
		for (let index = 0; index < lockPadding.length; index++) {
			const el = lockPadding[index];
			el.style.paddingRight = lockPaddingValue;
		}
	}
	body.style.paddingRight = lockPaddingValue;
	if (!body.classList.contains('lock')) {
		bodyWasNotLock = true;
		if (isiPhone || isiPad || isiPod) {
			disableScroll();
		} else {
			body.classList.add('lock');
		}
	} else {
		bodyWasNotLock = false;
	}
	unlock = false;
	setTimeout(function () {
		unlock = true;
	}, timeout);
}

function bodyUnlock() {
	setTimeout(function () {
		if (lockPadding.length > 0) {
			for (let index = 0; index < lockPadding.length; index++) {
				const el = lockPadding[index];
				el.style.paddingRight = '0px';
			}
		}
		body.style.paddingRight = '0px';
		if (bodyWasNotLock == true) {
			if (isiPhone || isiPad || isiPod) {
				enableScroll();
			} else {
				body.classList.remove('lock');
			}
		}
	}, timeout);
}

document.addEventListener('keydown', function (e) {
	if (document.querySelector('.modal.open')) {
		if (e.which === 27) {
			const modalActive = document.querySelector('.modal.open');
			modalClose(modalActive);
		}
	}
});
(function () {
	if (!Element.prototype.closest) {
		Element.prototype.closest = function (css) {
			var node = this;
			while (node) {
				if (node.matches(css)) return node;
				else node = node.parentElement;
			}
			return null;
		};
	}
})();
(function () {
	if (!Element.prototype.matches) {
		Element.prototype.matches = Element.prototype.matchesSelector ||
			Element.prototype.webkitMatchesSelector ||
			Element.prototype.mozMatchesSelector ||
			Element.prototype.msMatchesSelector;
	}
})();;
	// -- //
})